header {
    background-color: blueviolet;
    padding: 16px 0;
}

header h1 {
    color: white;
    font-size: 40px;
    font-weight: 100;
    font-style: italic;
    display: inline-block;
    position: relative;
    bottom: 35px;
    margin-left: 10px;
}

footer {
    background-color: blue;
    clear: both;
    color: aqua;
    text-align: center;
    padding: 50px 0;
    margin-top: 20px;
}


nav {
    background-color: aquamarine;
    margin-bottom: 20px;
}

nav ul {
    list-style: none;
    margin: 0;
    padding: 0%;
    display: flex;
}

nav ul li a {
    color: white;
    text-decoration: none;
    padding: 3px 6px;
    display: block;
    text-align: center;
}

nav ul li a:hover {
    background-color: blue;
}

nav ul li a:activo {
    background-color: lightblue;
}
